.question_form {
    background-color: #F4F4F9;
    height: 100%;
    padding-bottom: 30px;
}

.section {
    margin: auto;
    width: 50%;
}

.question_form_top {
    background-color: white;
    border-top: 8px solid rgb(103, 58, 183);
    border-radius: 8px;
    padding: 30px 25px;
    text-transform: capitalize;
}

.question_form_para {
    background-color: white;
    border-radius: 8px;
    padding: 30px 25px;
    text-transform: capitalize;
    border: 1px solid rgb(231, 231, 231);
    margin: 15px;
}

.btn {
    color: #fff;
    background: blue;
    border: none;
    border-radius: 5px;
    padding: 8px 20px;
    font-weight: 700;
    cursor: pointer;
}

.btn:focus:hover {
    color: #fff;
    background: rgb(87, 87, 248);
    border: none;
    border-radius: 5px;
    padding: 8px 20px;
    font-weight: 700;
    cursor: pointer;
}

input[type="range"] {
    writing-mode: sideways-lr !important;
}

input[type="range" i] {
    color: green !important;
}

.question_form_top_name {
    box-sizing: border-box;
    font-family: 'Google Sans', Roboto, Arial, sans-serif;
    font-size: 32px;
    font-weight: 400;
    line-height: 40px;
    line-height: 135%;
    width: 100%;
    border: none;
    outline: none;
    border-bottom: 1px solid #F4F4F9;
    color: black;
    height: 35px;
}

.question_form_top_desc {
    box-sizing: border-box;
    margin-top: 10px;
    font-family: 'Google Sans', Roboto, Arial, sans-serif;
    font-size: 13px;
    font-weight: 400;
    line-height: 40px;
    line-height: 135%;
    width: 100%;
    border: none;
    outline: none;
    border-bottom: 1px solid #c2c2c2;
    color: black;
    height: 20px;
}

.hr {
    height: 1vh !important;
    background: #00b539 !important;
    border: none !important;
    border-radius: 12px !important;
    display: grid !important;
    width: 20vw !important;
    margin: auto;
    border-top: 0 !important;
    opacity: 1 !important;
}